.dual-range {
  margin-bottom: 20px;
  --range-size: 12px;
  --range-width: 100%;
  --handle-size: 1.3;
  height: var(--range-size);
  width: var(--range-width);
  background: #304f47;
  border-radius: 50px;
  position: relative;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.dual-range .highlight {
  position: absolute;
  height: var(--range-size);
  left: var(--x-1);
  background: var(--clr-prim);
  z-index: 1;
  border-radius: 50px;
}

.dual-range .handle {
  height: 20px;
  width: 20px;
  background: #ff7f47;
  position: absolute;
  box-shadow: var(--shadow);
  border-radius: 50%;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
  cursor: grab;
}

.dual-range .handle:active {
  cursor: grabbing;
}

.dual-range .handle.left {
  left: var(--x-1);
}

.dual-range .handle.right {
  left: var(--x-2);
}

.dual-range .handle::after {
  content: '$' attr(data-value);
  position: absolute;
  top: 110%;
  left: 50%;
  transform: translateX(-50%);
  font-weight: 700;
  color: #304f47;
}

