@media only screen and (max-width: 1199px) {
  .main-banner-2 {
    background-size: cover;
  }
  .header-area .account-dropdown {
    top: 127px;
  }

  .sidebar-social {
    display: block;
  }

  .main-nav .sidebar-contact {
    display: block;
  }

  .navber-logo-sm {
    display: block;
  }

  // Responsive Navbar

  .main-nav ul li .sub-menu > li {
    border-bottom: none;
  }
  header .header-area .main-nav ul li ul.sub-menu > li a.sub-item {
    color: #fff;
    padding: 10px 0;
    display: inline-block;
  }
  header .header-area .main-nav {
    display: block;
  }
  .navbar-icons-2 {
    display: none !important;
  }
  .col-lg-2 {
    width: 100%;
  }
  .navbar-icons {
    display: inline-flex !important;
    align-items: center;

    .searchbar-open,
    .user-dropdown-icon {
      margin-left: 30px;
      i {
        font-size: 20px;
        color: $primary-color;

        &::before {
          vertical-align: middle;
        }
      }
    }
    .mobile-menu {
      margin-left: 40px;
    }
  }
  .hamburger {
    display: block !important;
    position: relative;
    width: 40px !important;
    height: 30px !important;
    margin: 1.8rem 0;
    cursor: pointer;
    border: 3px solid transparent !important;
    float: right;
    &:focus {
      border: 3px solid transparent !important;
    }
  }

  .top-search-bar {
    position: absolute;
    right: 10%;
    top: 3.5rem;
    a i::before {
      color: #707070;
      font-size: 2.4rem;
    }
  }

  header .header-area .main-nav ul li ul.sub-menu > li a::before {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0 !important;
    height: 2px;
    content: '';
    background-color: #ffffff;
    opacity: 0;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }

  .main-nav > ul > li > a {
    font-size: 16px !important;
    color: #fff !important;
    line-height: 35px;
    font-family: $secondary-font;
    font-weight: 500;
  }

  header .header-area .main-nav ul li a {
    padding: 0 !important;
  }
  header .header-area .main-nav ul li a::before {
    bottom: 0rem !important;
  }
  .main-nav ul > li a.active::before {
    opacity: 1 !important;
  }
  .main-nav ul > li a:hover {
    background: transparent;
  }

  .main-nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 250px;
    z-index: 99999;
    height: 100%;
    overflow: auto;
    background: #000;
    transform: translateX(-250px);
    transition: transform 0.3s ease-in;
  }

  .main-nav > ul > li.has-child-menu:after {
    display: none !important;
  }

  .hamburger span {
    width: 100%;
    height: 3px;
    background: $primary-color;
    display: block;
    position: absolute;
    right: 0;
    transition: all 0.3s;
  }

  .hamburger span.h-top {
    top: 0;
  }
  .hamburger span.h-bottom {
    // bottom: 0.2rem;
    bottom: 0;
    width: 27px;
  }
  .hamburger span.h-middle {
    top: 50%;
    transform: translateY(-50%);
    width: 25px;
  }
  .hamburger.h-active span {
    width: 80%;
  }
  .hamburger.h-active span.h-top {
    transform: rotate(45deg);
    top: 50%;
    margin-top: -1px;
  }
  .hamburger.h-active span.h-middle {
    transform: translateX(-30px);
    opacity: 0;
  }
  .hamburger.h-active span.h-bottom {
    transform: rotate(-45deg);
    bottom: 50%;
    margin-bottom: -1px;
  }

  .main-nav.slidenav {
    transform: translateX(0);
  }
  .main-nav > ul {
    float: none;
    margin-top: 50px !important;
  }
  .main-nav > ul > li.has-child-menu:after {
    content: none;
  }

  .main-nav > ul > li {
    display: block;
    padding: 0;
    margin-left: 0;
  }
  header .header-area .main-nav ul li {
    display: block;
    position: relative;
    padding: 3px 20px;
  }

  .main-nav ul li .fl {
    display: block;
    line-height: 35px;
    margin-right: 15px;
  }
  .main-nav ul li .sub-menu {
    position: static;
    min-width: 200px;
    background: transparent;
    border: none;
    opacity: 1;
    visibility: visible;
    box-shadow: none;
    transform: none;
    transition: none;
    display: none;
  }

  .main-nav ul li .sub-menu > li a {
    display: block;
    padding: 10px 0;
    color: #707070;
    border: none;
    line-height: 0.8;
    font-size: 14px;
    text-decoration: none;
    transition: all 0.3s;
  }
  li a.active {
    color: $secondary-color;
    font-weight: 600;
    font-size: 1.5rem;
  }
}
