  border-radius: 5px;
  font-weight: 600;
  font-size: 14px;
  color: #fff;
  position: relative;
  z-index: 9;

  &:hover {
    &::before {
      clip-path: circle(150% at 0% 0%);
    }
  }

  &::before {
    height: 100%;
    width: 100%;
    position: absolute;
    content: '';
    background: $primary-color;
    top: 0;
    left: 0;
    border-radius: 5px;
    clip-path: circle(27% at -3% 50%);
    transition: all 0.4s;
    z-index: -1;
  }
}

