.custom-select {
  position: relative;
  width: 100%;
}

.custom-select select {
  display: none;
}

.select-selected {
  background-color: #fff;
  color: #262339;
  padding: 17px 16px;
  cursor: pointer;
  user-select: none;
  border-radius: 5px;
  font-weight: 700;
  font-size: 13px;
  line-height: 16px;
  position: relative;

  &::before {
    position: absolute;
    content: '\203A';
    right: 12px;
    transform: rotate(90deg) translateX(-111%);
    font-size: 24px;
    top: 50%;
  }
}

.select-selected.select-arrow-active:after {
  border-color: transparent transparent #fff transparent;
  top: 7px;
}
.select-items div {
  padding: 12px 16px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
  user-select: none;
  font-weight: 700;
  font-size: 13px;
  line-height: 16px;
  color: #ff7f47;

  &:last-child {
    border: none;
  }
}
.select-items {
  position: absolute;
  background-color: #fff;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
  border-radius: 5px 5px 0 0;
  box-shadow: 7px 10px 40px 0px #0d344f1a;
}
.select-hide {
  display: none;
}

.select-items div:hover,
.same-as-selected {
  background-color: rgba(0, 0, 0, 0.1);
}

.calendar-input {
  position: relative;

  i {
    position: absolute;
    top: 38%;
    transform: translateY(-50%);
    right: 10px;
    vertical-align: middle;

    &::before {
      vertical-align: middle;
    }
  }
}

/*==== 2. preloader =====*/
.preloader {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 99999;
  background: #ffffff;
  top: 0;
  left: 0;
  font-size: 30px;
  vertical-align: center;

  .preloader-wrap {
    text-align: center;

    .preloader-logo {
      margin-top: 200px;
    }
  }

  .plane-path {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    border-bottom: 1px dashed $primary-color;

    .animi-plane {
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      animation: loading-plane 4s linear infinite;

      @keyframes loading-plane {
        0% {
          left: 0;
        }
        100% {
          left: 100%;
        }
      }
    }
  }
}

/*===== Preloader CSS END =======*/
