.dual-range {
  margin-bottom: 20px;
  --range-size: 12px;
  --range-width: 100%;
  --handle-size: 1.3;
  height: var(--range-size);
  width: var(--range-width);
  background: $secondary-color;
  border-radius: 50px;
  position: relative;
  user-select: none;
  .highlight {
    position: absolute;
    height: var(--range-size);
    //	width: calc(	calc(var(--x-2) - var(--x-1)) + calc(var(--range-size) * var(--handle-size)));
    left: var(--x-1);
    background: var(--clr-prim);
    z-index: 1;
    border-radius: 50px;
  }

  .handle {
    height: 20px;
    width: 20px;
    background: $primary-color;
    position: absolute;
    box-shadow: var(--shadow);
    border-radius: 50%;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;
    cursor: grab;
    &:active {
      cursor: grabbing;
    }

    &.left {
      left: var(--x-1);
    }

    &.right {
      left: var(--x-2);
    }

    &::after {
      content: '$' attr(data-value);
      position: absolute;
      top: 110%;
      left: 50%;
      transform: translateX(-50%);
      font-weight: 700;
      color: $secondary-color;
    }
  }
}

