              z-index: 12;

              &::before {
                position: absolute;
                content: '';
                height: 100%;
                top: 0;
                left: 50%;
                transform: translateX(-50%);
                width: 6px;
                z-index: -1;
                background: url(../images/package/p-shape.png) no-repeat;
